.Options_options__3yhaW {
    display: flex;
    flex-direction: column;

    background-color: #191e2c;
    color: #fff;
    border-radius: 5px;

    overflow: hidden;

    opacity: 0;
    display: hidden;
    z-index: -100;

    position: absolute;
    top: 20px;
    right: 10px;

    transition: opacity 0.3s ease-in;
}

.Options_options__3yhaW.Options_show__fSJpq {
    opacity: 1;
    display: flex;
    z-index: 100;
}

.Options_options__3yhaW .Options_option__133mk {
    text-decoration: none;
    color: #fff;
    padding: 10px 15px;
    cursor: pointer;

    transition: all 0.3s ease-in-out;
}

.Options_options__3yhaW .Options_option__133mk:hover {
    background-color: #000;
    color: #fff;
}

